.coaches {
  max-width: 1366px;
  margin: auto;
  padding: 16px 0 140px;

  @media screen and (max-width: 1199px) {
    padding: 10px 0 100px;
  }

  @media screen and (max-width: 767px) {
    padding: 3px 0 70px;
  }
}

.coaches__wrapper {
  position: relative;
  padding: 0 103px;

  @media screen and (max-width: 1199px) {
    padding: 0 101px;
  }

  @media screen and (max-width: 767px) {
    padding: 0 47px;
  }
}

.coaches__title {
  margin: 0 0 47px 2px;
  padding: 0;
  font-family: $ff-alumni-sans;
  font-weight: 600;
  font-size: 54px;
  line-height: 65px;
  letter-spacing: 0.03em;
  color: $color-sapphire;

  @media screen and (max-width: 1199px) {
    font-size: 42px;
    line-height: 50px;
    text-align: center;
    margin: 0 6px 31px 0;
  }

  @media screen and (max-width: 767px) {
    font-size: 32px;
    line-height: 38px;
    margin-bottom: 42px;
  }
}

.coaches__item {
  width: 100%;
  height: 100%;
  margin: 0 40px 0 0;
  position: relative;
  list-style-type: none;
  background: $color-sapphire;
  color: $color-default-white;

  @media screen and (max-width: 1199px) {
    margin: 0 30px 0 0;
  }

  @media screen and (max-width: 768px) {
    height: 294px;
  }

  @media screen and (max-width: 767px) {
    margin: 0 30px 0 0;
    height: 100%;
  }

  @media screen and (max-width: 320px) {
    height: 274px;
  }
}

.coaches__img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 35px rgba(42, 59, 99, 0.17);
}

.coaches__heading {
  margin: 0 0 21px;
  position: absolute;
  bottom: 0;
  left: 30%;
  font-family: $ff-roboto-condensed;
  font-weight: 700;
  font-size: 18px;
  line-height: 150%;
  place-self: center;


  @media screen and (max-width: 1199px) {
    left: 37%;
  }

  @media screen and (max-width: 900px) {
    left: 32%;
  }

  @media screen and (max-width: 767px) {
    left: 40%;
  }

  @media screen and (max-width: 500px) {
    left: 33%;
  }

  @media screen and (max-width: 320px) {
    left: 27%;
  }
}

.coaches__heading-index {
  left: 27%;

  @media screen and (max-width: 1199px) {
    left: 35%;
  }

  @media screen and (max-width: 900px) {
    left: 26%;
  }

  @media screen and (max-width: 767px) {
    left: 40%;
  }

  @media screen and (max-width: 500px) {
    left: 33%;
  }

  @media screen and (max-width: 320px) {
    left: 24%;
  }
}

.coaches__heading-next {
  left: 22%;

  @media screen and (max-width: 1199px) {
    left: 35%;
  }

  @media screen and (max-width: 900px) {
    left: 26%;
  }

  @media screen and (max-width: 767px) {
    left: 37%;
  }

  @media screen and (max-width: 500px) {
    left: 33%;
  }

  @media screen and (max-width: 320px) {
    left: 20%;
  }
}

.coaches__container {
  height: 10%;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  padding: 76px 40px 50px 40px;
  z-index: 1;

  @media screen and (max-width: 767px) {
    padding: 65px 33px 48px 33px;
  }
}

.coaches__container-next {
  position: relative;
  z-index: 1;
  place-self: start center;
  display: none;
  width: 100%;
}

.coaches__item:hover .coaches__container,
// .coaches__item:focus .coaches__container,
.coaches__item:focus-within .coaches__container {
  height: 100%;
  width: 100%;
  background-color: rgba(16, 38, 84, 0.89);
  overflow-y: scroll;
}

.coaches__item:hover .coaches__container-next,
// .coaches__item:focus .coaches__container-next,
.coaches__item:focus-within .coaches__container-next {
  display: block;
}

.coaches__item:hover .coaches__heading,
// .coaches__item:focus .coaches__heading,
.coaches__item:focus-within .coaches__heading {
  top: 52px;
  z-index: 10;

  @media screen and (max-width: 767px) {
    top: 42px;
  }
}

// @media screen and (max-width: 1199px) {
//   .coaches__item:active .coaches__container,
//   .coaches__item:focus .coaches__container,
//   .coaches__item:focus-within .coaches__container {
//     opacity: 1;
//     pointer-events: all;
//   }
// }

@media screen and (max-width: 1199px) {
  .coaches__item:active .coaches__heading,
  // .coaches__item:focus .coaches__heading,
  .coaches__item:focus-within .coaches__heading {
    top: 52px;
    z-index: 10;
  }
}

@media screen and (max-width: 767px) {
  .coaches__item:active .coaches__heading,
  // .coaches__item:focus .coaches__heading,
  .coaches__item:focus-within .coaches__heading {
    top: 42px;
  }
}

.coaches__container p {
  margin: 0 0 33px;
  padding: 0;
  font-family: $ff-roboto-condensed;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  align-items: center;
  color: $color-spindle;
  text-align: center;

  @media screen and (max-width: 767px) {
    margin: 0 0 34px;
  }
}

.coaches__container-list {
  margin: 0;
  padding: 0 0 0 31px;
  max-height: 130px;
  // overflow-y: scroll;

  @media screen and (max-width: 767px) {
    padding: 0 0 0 17px;
  }
}

.coaches__container-item {
  margin: auto;
  padding: 0;
  max-width: 300px;
  list-style-type: none;
  text-align: start;
}

.coaches__container-item p {
  position: relative;
  margin: 0 0 13px 0;
  padding: 0;
  font-size: 14px;
  color: $color-default-white;
  text-align: start;
}

.coaches__container-item p::after {
  position: absolute;
  content: "";
  width: 6px;
  height: 6px;
  top: 6px;
  left: -16px;
  background-color: $color-default-white;
  border-radius: 50%;
  z-index: 10;
}

.coaches .slider-button {
  top: 60%;

  @media screen and (max-width: 1199px) {
    top: 56%;
  }

  @media screen and (max-width: 767px) {
    top: 57%;
  }
}
